.features {
    width: 100%;
    max-width: 75rem;

    @media(max-width: $desktop_lg_width) {
        max-width: 100%;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        padding: 2.7rem 0 0 1rem;

        @media(max-width: $desktop_lg_width) {
            margin-bottom: 0.6rem;
        }

        @media(max-width: $desktop_sm_width) {
            padding-right: 1rem;
            margin-bottom: 0;
        }
    }
    
    &__item {
        min-height: 7.8rem;
        width: 22.6rem;
        border: 1px solid #E5E5E5;
        border-radius: 1rem;
        margin: 0 1rem;
        margin-bottom: 2rem;

        @media(max-width: $desktop_lg_width) {
            margin: 0 0.7rem;
            margin-bottom: 0.8rem;
        }

        @media(max-width: $desktop_sm_width) {
            width: 100%;
        }

        &-name {
            font-family: $font_sec-bold;
            font-size: 1.4rem;
            line-height: 1.4rem;
            margin-bottom: 0.6rem;

            color: $color_blue;
        }

        &-link {
            width: 100%;
            height: 100%;
            padding-left: 6.8rem;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;

            span {
                font-family: $font_sec-reg;
                font-size: 1.2rem;
                line-height: 1.2rem;
                text-decoration-line: underline;
    
                color: $color_darkblue;
                transition: color $transition_rules;
            }
    
            svg {
                width: 4rem;
                height: 4rem;
                position: absolute;
                top: 2.2rem;
                left: 1.4rem;
                object-fit: contain;
            }

            &:hover,
            &:focus {
                span {
                    color: $color_blue;
                }
            }
        }
    }
}