.purchase {
    width: 100%;
    max-width: 53.5rem;
    background-color: #F6F8FB;
    border-radius: 16px;

    padding: 2.5rem 1.6rem;

    @media(max-width: $desktop_lg_width) {
        max-width: 100%;
        margin-bottom: 1.6rem;
        order: 1;
    }

    @media(max-width: $tablet_width) {
        margin-bottom: 0;
    }

    &__header {
        display: flex;
        margin-bottom: 3.3rem;

        &-price {
            font-family: $font_exo-med;
            font-size: 3.6rem;
            line-height: 3.6rem;
            letter-spacing: -0.01em;

            color: $color_darkblue;
            margin-right: auto;
        }

        &-menu {
            display: flex;

            @media(max-width: $desktop_sm_width) {
                flex-direction: column;
            }
        }

        &-btn {
            position: relative;
            display: flex;
            align-items: center;
            font-family: $font_sec-reg;
            font-size: 1.1rem;
            line-height: 1.5rem;
            border: 1px solid #E1E7EB;
            border-radius: 6px;
            background-color: transparent;
            padding: 0.7rem 0.8rem;

            color: #45454E;

            transition: border $transition_rules, color $transition_rules;

            &:not(:last-child) {
                margin-right: 0.8rem;

                @media(max-width: $desktop_sm_width) {
                    margin-right: 0;
                    margin-bottom: 0.5rem;
                }
            }

            svg {
                margin-right: 0.8rem;
                transition: fill $transition_rules;
            }

            &:hover,
            &:focus {
                border: 1px solid $color_blue;
                color: $color_blue;

                svg {
                    fill: $color_blue;
                }
            }
        }
    }

    &__content {
        display: flex;
        
        margin-bottom: 2.4rem;

        &-info {
            &:not(:last-child) {
                min-width: 10rem;
                margin-right: 4.5rem;
            }

            &-name {
                font-family: $font_sec-reg;
                font-size: 1.4rem;
                line-height: 1.4rem;
                margin-bottom: 1.4rem;

                color: $color_darkblue;
            }

            &-desc {
                display: block;
                font-weight: 500;
                font-size: 2.4rem;
                line-height: 2.4rem;
                letter-spacing: -0.01em;

                span {
                    font-family: $font_exo-med;
                }

                color: $color_darkblue;
            }
        }
    }

    &__footer {
        button {
            font-size: 1.6rem;
            line-height: 1.6rem;
            width: 100%;
            padding: 1.5rem;
            

            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
        }
    }
}